.article-competence h1 {
  text-align: center;
  margin: 90px auto 20px auto;
  font-size: 45px;
  color: rgb(167, 165, 165);
}

.line {
  width: 500px;
  height: 1px;
  background-color: rgb(187, 185, 185);
  margin: 0px auto 20px auto;
}

.hover-info {
  font-size: 22px;
  text-align: center;
  color: rgb(119, 119, 119);
  margin-bottom: 40px;
}

.skill-cards-container {
  display: flex;
  justify-content: space-evenly;
  margin: 50px 0px;
  padding: 30px 20px;
  background-image: url(../../assets/images/cover-image.png);
  background-attachment: fixed;
  background-position: center;
  box-shadow: 10px 17px 23px 0px rgba(0, 0, 0, 0.19);
  -webkit-box-shadow: 10px 17px 23px 0px rgba(0, 0, 0, 0.19);
  -moz-box-shadow: 10px 17px 23px 0px rgba(0, 0, 0, 0.19);
}

.skill-cards {
  width: 50%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  height: 170px;
  width: 170px;
  border: 1px solid rgb(187, 185, 185);
  border-radius: 15px;
  margin: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  transition: all 0.5s ease-in-out;
  background-color: white;
}

.card.active {
  transform: scale(1.07);
  box-shadow: 5px 5px 15px -6px #000000;
  background-color: #e9e9f0;
  border: solid 1px #d5a9ff;
}

.card.active img {
  opacity: 0.1;
}

.card.active .skill-info {
  opacity: 1;
}

.card img {
  width: 80%;
  height: auto;
}

.card:hover {
  transform: scale(1.07);
  box-shadow: 5px 5px 15px -6px #000000;
}

.skill-info {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: all 1s ease-in-out;
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.skill-info.active {
  opacity: 1;
}

.skill-info h2 {
  border-bottom: solid 1px #b77de6;
}

.skill-info h2,
p {
  font-size: 1.3em;
  font-weight: 800;
  color: rgb(143, 141, 141);
  font-style: italic;
}

@keyframes animateLeftToRight {
  from {
    opacity: 0;
    transform: translateX(-60%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes animateRightToLeft {
  from {
    opacity: 0;
    transform: translateX(60%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.article-competence.animation-triggered .animate-left-to-right {
  opacity: 0;
  animation: animateLeftToRight 2s forwards;
}

.article-competence.animation-triggered .animate-right-to-left {
  opacity: 0;
  animation: animateRightToLeft 2s forwards;
}

.section-connaissances {
  width: 100%;
  flex-wrap: wrap;
  margin-bottom: 80px;
  padding: 0px 30px;
}

.section-connaissances aside {
  text-align: center;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.section-connaissances aside p {
  margin: 10px 30px;
  border: solid 1px rgb(207, 206, 206);
  border-radius: 25px;
  padding: 7px 15px;
  box-shadow: 5px 5px 15px -6px #c4c2c2;
}

.section-connaissances aside p i {
  margin: 0px 7px;
  color: rgb(14, 192, 14);
}

@media (max-width: 768px) {
  .line {
    width: 250px;
  }

  .article-competence h1 {
    margin-top: 35px;
  }

  .article-competence h1 {
    font-size: 40px;
  }

  .section-connaissances {
    padding: 0px;
  }
}
